import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom"
import { useState, useEffect, useCallback } from "react"
import MoonLoader from "react-spinners/ClipLoader"
import { contentfulNormalizer } from './normalizer/contentfulNormalizer'
import { graphQlQuery } from './graphQL/contentfulQuery'
// import { Seo } from './components/atoms/Seo'
// import { AppRoutes } from './routes/AppRoutes'
import ReactGA from 'react-ga'
import { locales } from './config/locales'
import { capitalizeCountryFromLocale } from './helpers/transformLocale'
// import { Seo } from './components/atoms/Seo'
import { Home } from './components/organisms/Home'
import { ProjectPage } from './components/organisms/ProjectPage'
import { NotFound } from './components/organisms/NotFound'
import { Navigation } from './components/molecules/Navigation'
import { Footer } from './components/molecules/Footer'
ReactGA.initialize(process.env.REACT_APP_GOOGLE_TRACKING_ID)

const App = () => {
  const [pageContent, setPageContent] = useState(undefined)
  const [locale, setLocale] = useState(undefined)

  const fetchContent = useCallback(async () => {
    fetch(`https://graphql.contentful.com/content/v1/spaces/${process.env.REACT_APP_CONTENTFUL_SPACE_ID}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${process.env.REACT_APP_CONTENTFUL_ACCESS_TOKEN}`,
        },
        body: JSON.stringify({ query: graphQlQuery(capitalizeCountryFromLocale(locale)) }),
      })
      .then(async (response) => {
        const data = await response.json()
        setPageContent(contentfulNormalizer(data, locale))
      })
      .catch(error => console.log('There was an error:', error))
  }, [locale])

  useEffect(() => {
    fetchContent()
  }, [fetchContent])

  useEffect(() => {
    if (locale && !Object.keys(locales).find(key => locales[key] === locale)) {
      window.location.href = locale ? `${locale}/404` : '/404'
    }
  }, [locale])

  useEffect(() => {
    ReactGA.pageview(window?.location?.pathname + window?.location?.search);
  }, [])

  if (!pageContent) {
    return (
      <div className="grid place-items-center h-screen">
        <MoonLoader
          size={120}
        />
      </div>
      )
  }

  return (
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Navigation logo={pageContent?.assets?.logo} menuItems={pageContent?.menuItems} locale={locale} projectsContent={pageContent?.project?.projects}/>
        <Routes>
            <Route exact path="/" element=<Home pageContent={pageContent} setLocale={setLocale} locale={locale}/>/>
            <Route path="/:locale" element=<Home pageContent={pageContent} setLocale={setLocale} locale={locale}/>/>
            <Route path="/projects/:id" element=<ProjectPage projectsContent={pageContent?.project?.projects} setLocale={setLocale} translations={pageContent?.translations} menuItems={pageContent?.menuItems}/> />
            <Route path="/:locale/projects/:id" element=<ProjectPage projectsContent={pageContent?.project?.projects} setLocale={setLocale} translations={pageContent?.translations} menuItems={pageContent?.menuItems}/> />
            <Route path='/404' element={<NotFound headerContent={pageContent?.headers} setLocale={setLocale}/>} />
            <Route path='*' element={<Navigate replace to='/404'/>} />
        </Routes>
        <Footer logo={pageContent?.assets?.logo} footerItems={pageContent?.menuItems} socialMedia={pageContent?.assets?.socialMedia} locale={locale}/>
    </BrowserRouter>
  )
}

export default App